export default function RecentProjects() {
  const projects = [
    {
      id: 'portfolio',
      title: 'Personal Portfolio Website',
      description: 'A responsive portfolio website built with React and Tailwind CSS, featuring modern design principles and smooth user interactions.',
      image: '/portfolio.jpg',
      technologies: ['Next.JS', 'Tailwind CSS', 'Typescript']
    },
    {
      id: 'student-internship-tracker',
      title: 'Student Internship Tracker',
      description: 'Student Task Management System built using Next.JS',
      image: '/Tracker.jpg',
      technologies: ['Next.JS', 'Tailwind CSS', 'Typescript']
    }
  ];

  return (
    <section id="projects" className="w-full px-8 py-16 bg-white">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-12">
          <h2 className="text-4xl text-gray-900 mb-4">Recent Projects</h2>
          <p className="text-gray-600 text-lg max-w-2xl mx-auto">
            Discover my latest projects showcasing my development 
            and design skills across different technologies and platforms.
          </p>
          <p className="text-gray-500 text-sm mt-2">
            Here are some of my recent portfolio showcasing my development 
            and design capabilities.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {projects.map((project) => (
            <div key={project.id} className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
              <div className="aspect-video">
                {project.id === 'portfolio' ? (
                  <img 
                    src={project.image} 
                    alt={project.title}
                    className="w-full h-full object-cover"
                  />
                ) : (
                  <img
                    src={project.image} 
                    alt={project.title}
                    className="w-full h-full object-cover"
                  />
                )}
              </div>
              
              <div className="p-6">
                <h3 className="text-xl text-gray-900 mb-3">{project.title}</h3>
                <p className="text-gray-600 text-sm leading-relaxed mb-4">
                  {project.description}
                </p>
                
                <div className="flex flex-wrap gap-2 mb-4">
                  {project.technologies.map((tech) => (
                    <span 
                      key={tech}
                      className="text-xs px-2 py-1 bg-gray-100 text-gray-700 rounded"
                    >
                      {tech}
                    </span>
                  ))}
                </div>
                
                <button className="bg-green-600 hover:bg-green-700 text-white px-6 py-2 text-sm rounded-md w-full">
                  View Project
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}